.Profile{
    font-family : 'ONE-Mobile-Title';
}

/* pc */
.Profile_PC-banner{
    padding-left: 50px;
    height: 300px;
}

.Profile_PC-banner-left{
    width: 250px;
    height: 250px;
    border-radius: 100%;
    background-color: var(--mainWidgetcolor);
}

.Profile_PC-banner-left > img{
    height: 190px;
}

.Profile_PC-banner-right > div {
    font-size: 18px;
    padding-left: 5px;
    padding-bottom: 4px;
    width: 360px;
}

.Profile_PC-banner-right > div:first-child {
    font-size: 46px;
    font-weight: bold;
    padding-left: 0;
    padding-bottom: 4px;
}

.Profile_PC-banner-tabs-menu{
    padding-right: 30px;
}

.Profile_PC-banner-tabs-menu{
    border-bottom-width: 2px;
    border-bottom-color: black;
    font-size: 18px;
}

.Profile_PC-banner-tabs-menu > li{
    width: 150px;
    height: 50px;
}

.Profile_PC-banner-tabs-menu > li.Choosed{
    background-color: white;
    border: 2px solid black;
    border-bottom : none;
    position: relative;
}

.Profile_PC-banner-tabs-menu > li.Choosed::after{
    content : "";
    position: absolute;
    top:100%;
    left:0; 
    width : 100%;
    height: 2px;
    background-color: white;
}

.Profile_PC-banner-tabs-pages {
    /* background-color: white; */
}

/* mobile */
.Profile_MB-banner{
    padding-top: 30px;
}

.Profile_MB-banner-top{
    width: 270px;
    height: 270px;
    border-radius: 135px;
    background-color: var(--mainWidgetcolor);
}

.Profile_MB-banner-top > img{
    height: 190px;
}

.Profile_MB-banner-bottom{
    padding-left: 30px;
}

.Profile_MB-banner-bottom > div {
    font-size: 18px;
    padding-left: 5px;
    padding-bottom: 4px;
}

.Profile_MB-banner-bottom > div:first-child {
    font-size: 42px;
    font-weight: bold;
    padding-left: 0;
    padding-bottom: 8px;
}

.Profile_MB-banner-tabs-menu{
    border-bottom-width: 2px;
    border-bottom-color: black;
    font-size: 18px;
}

.Profile_MB-banner-tabs-menu > li{
    width: 150px;
    height: 50px;
}

.Profile_MB-banner-tabs-menu > li.Choosed{
    background-color: white;
    border: 2px solid black;
    border-bottom : none;
    position: relative;
}

.Profile_MB-banner-tabs-menu > li:first-child{
    border-left: none;
}

.Profile_MB-banner-tabs-menu > li:last-child{
    border-right: none;
}

.Profile_MB-banner-tabs-menu > li.Choosed::after{
    content : "";
    position: absolute;
    top:100%;
    left:0; 
    width : 100%;
    height: 2px;
    background-color: white;
}

@media only screen and (max-width: 900px) {
    .Profile_PC{
        display: none;
    }
    .Profile_MB {
        display: flex;
    }
}

@media only screen and (min-width: 901px) {
    .Profile_PC{
        display: flex;
    }
    .Profile_MB {
        display: none;
    }
}