
.Career{
    background-color: white;
}

/* pc */
.Career_PC{
    padding: 100px 0 0 100px;
}

.Career_PC-left{
    font-size: 30px;
    font-weight: bold;
    width: 220px;
}

.Career_PC-right > div:first-child{
    width: 30%;
    padding-right: 70px;
    color : var(--mainDurationColor);
}

/* mobile */
.Career_MB{
    padding: 80px 30px 0 30px;
}

.Career_MB-top{
    font-size: 30px;
    font-weight: bold;
}

.Career_MB-bottom > div:first-child{
    color : var(--mainDurationColor);
}

@media only screen and (max-width: 900px) {
    .Career_PC{
        display: none ;
    }
    .Career_MB {
        display: flex;
    }
}

@media only screen and (min-width: 901px) {
    .Career_PC{
        display: flex ;
    }
    .Career_MB {
        display: none;
    }
}