
.Certificate{
    background-color: white;
}

/* pc */
.Certificate_PC{
    padding: 100px 0 0 100px;
}

.Certificate_PC-left{
    font-size: 30px;
    font-weight: bold;
    width: 220px;
}

.Certificate_PC-right > div:first-child{
    width: 30%;
    padding-right: 70px;
    color : var(--mainDurationColor);
}

/* mobile */
.Certificate_MB{
    padding: 80px 30px 0 30px;
}

.Certificate_MB-top{
    font-size: 30px;
    font-weight: bold;
}

.Certificate_MB-bottom > div:first-child{
    color : var(--mainDurationColor);
}

@media only screen and (max-width: 900px) {
    .Certificate_PC{
        display: none ;
    }
    .Certificate_MB {
        display: flex;
    }
}

@media only screen and (min-width: 901px) {
    .Certificate_PC{
        display: flex ;
    }
    .Certificate_MB {
        display: none;
    }
}