.Works{
    font-family : 'ONE-Mobile-Title';
}

/* pc */
.Works_PC-banner{
    padding-left: 50px;
    height: 300px;
}

.Works_PC-banner-left{
    font-size: 64px;
    font-weight: bold;
}

.Works_PC-banner-right {
    font-size: 32px;
}

.Works_PC-banner-tabs-menu{
    padding-right: 30px;
}

.Works_PC-banner-tabs-menu{
    border-bottom-width: 2px;
    border-bottom-color: black;
    font-size: 18px;
}

.Works_PC-banner-tabs-menu > li{
    width: 150px;
    height: 50px;
}

.Works_PC-banner-tabs-menu > li.Choosed{
    background-color: white;
    border: 2px solid black;
    border-bottom : none;
    position: relative;
}

.Works_PC-banner-tabs-menu > li.Choosed::after{
    content : "";
    position: absolute;
    top:100%;
    left:0; 
    width : 100%;
    height: 2px;
    background-color: white;
}

/* mobile */
.Works_MB-banner{
    padding: 50px 0 30px 30px;
}

.Works_MB-banner-top{
    font-size: 48px;
    font-weight: bold;
}

.Works_MB-banner-bottom{
    padding-left: 8px;
    font-size: 20px;
}

.Works_MB-banner-tabs-menu{
    border-bottom-width: 2px;
    border-bottom-color: black;
    font-size: 18px;
}

.Works_MB-banner-tabs-menu > li{
    width: 150px;
    height: 50px;
}

.Works_MB-banner-tabs-menu > li.Choosed{
    background-color: white;
    border: 2px solid black;
    border-bottom : none;
    position: relative;
}

.Works_MB-banner-tabs-menu > li:first-child{
    border-left: none;
}

.Works_MB-banner-tabs-menu > li:last-child{
    border-right: none;
}

.Works_MB-banner-tabs-menu > li.Choosed::after{
    content : "";
    position: absolute;
    top:100%;
    left:0; 
    width : 100%;
    height: 2px;
    background-color: white;
}

@media only screen and (max-width: 900px) {
    .Works_PC{
        display: none;
    }
    .Works_MB {
        display: flex;
    }
}

@media only screen and (min-width: 901px) {
    .Works_PC{
        display: flex;
    }
    .Works_MB {
        display: none;
    }
}